.signUp {
  padding-top: 70px;
  padding-bottom: 106px;
  text-align: center;

  @include mq(desktop) {
    padding-top: 84px;
    padding-bottom: 92px;
  }

  &__form {
    margin-left: auto;
    margin-right: auto;
    max-width: 722px;
    font-size: 15px;

    @include mq(tablet) {
      width: 722px;
    }
  }

  &__title {
    position: relative;
    margin-bottom: 76px;
    font-weight: 500;
    font-size: 28px;
    letter-spacing: 0.08em;
    color: $main-text-color;

    &::before {
      content: '';
      position: absolute;
      bottom: -37px;
      left: 130px;
      display: block;
      max-width: 461px;
      height: 1px;
      background-color: $decore-background-color;
      background-color: red;
    }

    @include mq(tablet) {
      font-size: 48px;
    }
  }

  &__subtitle {
    margin-bottom: 35px;
    margin-left: auto;
    margin-right: auto;
    max-width: 647px;
    font-family: $font-family-title, sans-serif;
    line-height: 1.56;
    text-align: center;
    font-size: 15px;

    @include mq(tablet) {
      font-size: 18px;
    }
  }

  &__input-wrapper {
    margin-bottom: 20px;
    display: flex;
    gap: 20px;

    @media screen and (max-width: 620px) {
      flex-direction: column;
    }

    @include mq(tablet) {
      flex-direction: row;
      justify-content: space-between;
      margin-bottom: 58px;
    }
  }

  &__name {
    padding-left: 36px;
    width: 100%;
    height: 48px;
    background: $input-background-color;
    border: none;
    border-radius: 20px 0;

    @include mq(tablet) {
      width: 317px;
    }

    &.email {
      margin-bottom: 50px;
      width: 100%;

      @include mq(tablet) {
        margin-bottom: 92px;
      }
    }
  }
}

.about__btn.btn.submit {
  width: 180px;
  height: 50px;
  border-radius: 0 0 20px 0;
}
