body {
  color: #646464;
  background-color: #fff;
  font-family: Poppins, sans-serif;
  font-size: 18px;
  font-weight: 400;
}

.stopScroll {
  height: 100vh;
  overflow: hidden;
}

.btn {
  background-color: #37806b;
  border-radius: 20px 0;
  padding: 10px 20px;
  transition: all .5s cubic-bezier(.4, 0, .2, 1);
}

.btn:hover, .btn:focus {
  background-color: #caa892;
  transform: scale(1.1);
}

html {
  scroll-behavior: smooth;
}

h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
  display: block;
}

button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border-style: none;
  padding: 0;
}

.container {
  max-width: 760px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 25px;
  padding-right: 10px;
}

@media screen and (min-width: 1060px) and (max-width: 1439px) {
  .container {
    max-width: 1060px;
    padding-left: 25px;
    padding-right: 10px;
  }
}

@media screen and (min-width: 1440px) {
