.preloader {
  width: 100%;
  height: 100%;
  z-index: 2000;
  background-color: rgba(255, 225, 188, .3);
  position: fixed;
  top: 0;
  left: 0;
}

.preloader__loader {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.spinner {
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform-origin: 0 0;
  position: relative;
  transform: translateZ(0)scale(1);
}

.spinner div {
  width: 20px;
  height: 80px;
  box-sizing: content-box;
  animation: spinnerAnim 1s cubic-bezier(.5, 0, .5, 1) infinite;
  position: absolute;
  top: 60px;
}

.spinner div:nth-child(1) {
  background: #611f0a;
  animation-delay: -.6s;
  transform: translate(30px);
}

.spinner div:nth-child(2) {
  background: #8d4b1f;
  animation-delay: -.4s;
  transform: translate(70px);
}

.spinner div:nth-child(3) {
  background: #b46f39;
  animation-delay: -.2s;
  transform: translate(110px);
}

.spinner div:nth-child(4) {
  background: #c8975c;
  animation-delay: -1s;
  transform: translate(150px);
}

.spinner div:nth-child(5) {
  background: #d6ae7c;
  animation-delay: -1.2s;
  transform: translate(190px);
}

.spinner__bars {
  width: 240px;
  height: 200px;
  background: none;
  display: inline-block;
  overflow: hidden;
}

@keyframes spinnerAnim {
  0% {
    opacity: 1;
    height: 80px;
  }

  50% {
    opacity: .5;
    height: 50px;
  }

  100% {
    opacity: 1;
    height: 80px;
  }
}

